import React, { useState, useEffect } from "react";
import { useNavigate } from "react-router-dom";
import axios from "axios";
import bcrypt from "bcryptjs";
import styled from "styled-components";
import Logo from "../assets/image/Logo.svg";
import EyeIconOff from "../assets/image/eye-off.png";
import EyeIconOn from "../assets/image/eye-on.png";
const BASE_URL = "http://15.165.252.35:1936";

export default function Login() {
  const [username, setUsername] = useState("");
  const [password, setPassword] = useState("");
  const [showPassword, setShowPassword] = useState(false);
  const [autoLogin, setAutoLogin] = useState(false);
  const [errorMessage, setErrorMessage] = useState("");
  const navigate = useNavigate();

  //   
  useEffect(() => {
    const accessToken = localStorage.getItem("accessToken");
    const savedAutoLogin = localStorage.getItem("autoLogin") === "true";
    setAutoLogin(savedAutoLogin);
    if (accessToken) {
      navigate("/");
    }
  }, [navigate]);

  // 
  const handleSubmit = async (e) => {
    e.preventDefault();
    //    
    if (!username || !password) {
      setErrorMessage("  .");
      return;
    }

    //     
    try {
      const response = await axios.post(`${BASE_URL}/auth/signin`, {
        username,
        password,
      });
      //  ,
      //  ,    ,   ,   
      console.log(response.data);
      console.log(autoLogin);
      setErrorMessage("");
      const hashed = await bcrypt.hash(password, 10);
      //console.log(bcrypt.hash(password, 10));
      localStorage.setItem("hashed", hashed);
      localStorage.setItem("autoLogin", autoLogin.toString());
      localStorage.setItem(
        "accessToken",
        response.data.data.tokenDto.accessToken
      );
      localStorage.setItem(
        "refreshToken",
        response.data.data.tokenDto.refreshToken
      );
      axios.defaults.headers.common[
        "Authorization"
      ] = `Bearer ${response.data.data.tokenDto.accessToken}`;
      console.log(axios.defaults.headers.common.Authorization);
      navigate("/");
      //   
    } catch (error) {
      if (error.response && error.response.status === 400) {
        setErrorMessage("   .");
      } else {
        setErrorMessage(" .  .");
        console.log("Error", error.message);
      }
    }
  };

  //   
  axios.interceptors.response.use(
    (response) => response,
    async (error) => {
      const isAutoLoginPossible = localStorage.getItem("autoLogin") === "true";
      if (
        isAutoLoginPossible &&
        error.response.status === 401 &&
        !error.config._retry
      ) {
        error.config._retry = true;
        try {
          const tokenResponse = await axios.post(
            `${BASE_URL}/auth/reissue-token`,
            {
              accessToken: localStorage.getItem("accessToken"),
              refreshToken: localStorage.getItem("refreshToken"),
            }
          );
          localStorage.setItem(
            "accessToken",
            tokenResponse.data.data.accessToken
          );
          localStorage.setItem(
            "refreshToken",
            tokenResponse.data.data.refreshToken
          );
          axios.defaults.headers.common[
            "Authorization"
          ] = `Bearer ${tokenResponse.data.data.accessToken}`;
          return axios(error.config);
        } catch (reissueError) {
          console.error("Token Expired. Redirecting to login page.");
          window.alert(
            " .   ."
          );
          localStorage.removeItem("accessToken");
          localStorage.removeItem("refreshToken");
          localStorage.removeItem("autoLogin");
          localStorage.removeItem("hashed");
          navigate("/login"); //         
          return Promise.reject(reissueError);
        }
      }
      return Promise.reject(error);
    }
  );

  //       
  axios.interceptors.request.use(
    (config) => {
      const authToken = localStorage.getItem("accessToken");
      if (authToken) {
        config.headers["Authorization"] = `Bearer ${authToken}`;
      }
      return config;
    },
    (error) => Promise.reject(error)
  );

  const handlePasswordToggle = () => {
    setShowPassword(!showPassword);
  };

  return (
    <Background>
      <LoginFormWrap>
        <LogoImg src={Logo} alt=" " />
        <AdminText>  </AdminText>
        <DetailText>
             
          <br />
           
        </DetailText>
        <form onSubmit={handleSubmit}>
          <InputWrap>
            <IDInput
              placeholder="ID"
              type="text"
              value={username}
              onChange={(e) => setUsername(e.target.value)}
              maxLength={25}
              hasError={errorMessage}
            ></IDInput>
            <PWInputWrap>
              <PWInput
                placeholder="PW"
                type={showPassword ? "text" : "password"}
                value={password}
                onChange={(e) => setPassword(e.target.value)}
                maxLength={25}
                hasError={errorMessage}
              ></PWInput>
              {showPassword ? (
                <EyeButton src={EyeIconOn} onClick={handlePasswordToggle} />
              ) : (
                <EyeButton src={EyeIconOff} onClick={handlePasswordToggle} />
              )}
            </PWInputWrap>
            {errorMessage && <ErrorText>{errorMessage}</ErrorText>}
          </InputWrap>
          <AutoLoginWrap>
            <CheckInput
              type="checkbox"
              checked={autoLogin}
              onChange={(e) => setAutoLogin(e.target.checked)}
            ></CheckInput>
            <CheckLabel> </CheckLabel>
          </AutoLoginWrap>
          {autoLogin && (
            <CheckText>
                ,   30 .
            </CheckText>
          )}
          <LoginButton type="submit"></LoginButton>
        </form>
      </LoginFormWrap>
    </Background>
  );
}

const Background = styled.div`
  display: flex;
  width: 100vw;
  height: 100vh;
  justify-content: center;
  align-items: center;
  background-color: #f6f9fd;
`;

const LoginFormWrap = styled.div`
  display: flex;
  flex-direction: column;
  width: 500px;
  height: 660px;
  justify-content: center;
  align-items: center;
  background-color: #fff;
`;

const LogoImg = styled.img`
  width: 60px;
  height: 60px;
`;

const AdminText = styled.text`
  color: #a055ff;
  font-size: 23px;
  margin-top: 20px;
  margin-bottom: 20px;
`;

const DetailText = styled.text`
  color: #222222;
  font-size: 15px;
  text-align: center;
  margin-bottom: 50px;
`;

const InputWrap = styled.div`
  display: flex;
  flex-direction: column;
  gap: 7px;
`;

const IDInput = styled.input`
  width: 330px;
  height: 35px;
  border: 1px solid ${(props) => (props.hasError ? "red" : "#D6DFE9")};
  padding: 5px;

  &:focus {
    border: 1px solid #a055ff;
  }
  &::placeholder {
    font-size: 15px;
    padding: 5px;
  }
`;

const PWInputWrap = styled.div`
  position: relative;
  display: flex;
  flex-direction: column;
`;

const PWInput = styled.input`
  width: 330px;
  height: 35px;
  border: 1px solid ${(props) => (props.hasError ? "red" : "#D6DFE9")};
  padding: 5px;

  &:focus {
    border-color: 1px solid #a055ff;
  }
  &::placeholder {
    font-size: 15px;
    padding: 5px;
  }
`;

const EyeButton = styled.img`
  position: absolute;
  bottom: 15px;
  right: 15px;
  width: 17px;
  height: 17px;
  cursor: pointer;
`;

const ErrorText = styled.text`
  font-size: 14px;
  color: red;
  margin-top: 3px;
`;

const AutoLoginWrap = styled.div`
  display: flex;
  flex-direction: row;
  gap: 5px;
  margin-top: 25px;
  margin-botton: 25px;
`;

const CheckInput = styled.input`
  width: 20px;
  height: 20px;
  outline: 1px solid #d6dfe9;
  border-radius: none;
  accent-color: #a055ff;
`;

const CheckLabel = styled.text`
  color: gray;
  font-size: 15px;
`;

const CheckText = styled.div`
  font-size: 14px;
  color: #a055ff;
  margin-top: 10px;
  margin-botton: 10px;
`;

const LoginButton = styled.button`
  width: 340px;
  height: 50px;
  margin-top: 25px;
  font-size: 18px;
  background-color: #979cac;
  color: white;
  border: none;

  &:hover {
    background-color: #2f395a;
  }
`;
